.navigation_bar {
    display: flex;
    justify-content: space-between;
    align-items: center;

    width: 100%;
    height: 85px;

    background-color: var(--color-border);
}

.navigation_bar-links {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.navigation_bar-logo {
    margin: 0 1.5rem;
}

.navigation_bar-logo img {
    height: 4rem;
}

.navigation_bar-links_components {
    display: flex;
    flex-direction: row;
}

.navigation_bar-links_components p {
    color: var(--color-accent);
    font-family: var(--font-family);
    font-weight: bold;
    font-size: 16px;
    line-height: 18px;
    align-items: center;
    text-align: center;

    margin: 0 1rem;
    cursor: pointer;
}

.navigation_bar-links-resume {
    border: 2px solid #7EFFD1;
    box-sizing: border-box;
    border-radius: 3px;
    margin: 0 1rem;
}

.navigation_bar-links-resume button {
    padding: 0.5rem 1rem;
    color: var(--color-accent);

    font-family: var(--font-family);
    font-weight: bold;
    font-size: 16px;
    line-height: 18px;

    border: 0;
    outline: none;
    background: none;
    cursor: pointer;
}
