@import url("https://fonts.googleapis.com/css2?family=Titillium+Web:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600;1,700&display=swap");
@import url('https://fonts.googleapis.com/css2?family=JetBrains+Mono&display=swap');

:root {
    --font-family: "Titillium Web", sans-serif;
    --font-tags: "Jetbrains Mono", monospace;

    --color-background: #101E38;
    --color-border: #11203D;
    --color-accent: #7EFFD1;

    --color-text-main: #7788AA;
    --color-text-title: #DEE5F3;
    --color-text-subtitle: #97ABD4;
    --color-text-subtitle-alt: #A3B3D4;
    --color-text-tags: #436CBE;

    --gradient-projects: linear-gradient(96.66deg, #1C2543 30.37%, #13273A 88.77%);
}