.about {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 40rem;
}

.about-container {
    width: 55rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.about-content {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    justify-content: space-around;
}

.about-content h1 {
    font-family: var(--font-family);
    font-weight: normal;
    font-size: 24px;
    line-height: 18px;
    display: flex;
    align-items: center;
    color: var(--color-accent);
}

.about-content p {
    font-family: var(--font-family);
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    width: 30rem;
    color: var(--color-text-main);
}

.about-image {
}

.about-image img {
    width: 15rem;
}
