* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
}

body {
    background: var(--color-background);
}

a {
    color: unset;
    text-decoration: none;
}

.divider {
    width: 36px;
    height: 4px;
    margin: 1rem 0 1.5rem 0;
    background: var(--color-text-main);
}

